
const isString = (str) => {
  const result = (typeof str === 'string');
  return result;
};

const isEffectiveString = (str) => {
  const result = !!(str && isString(str));
  return result;
};

const collectClasses = (classes) => {
  if (!isEffectiveString(classes)) throw new TypeError(`classes ${classes}`);
  return classes.replace(/\s+/g, ' ').split(' ');
};

let isGlobalInited = 0;

class Dom {
  constructor(query) {
    //  1.Dom 2.HTMLElement 3.tagName 4.querySelector
    // querySelector Dom.all(query)
    if (!query) throw new TypeError('');
    if (query instanceof Dom) {
      // Dom
      this.dom = query.dom;
    } else if (query instanceof HTMLElement) {
      // 
      this.dom = query;
    } else if (isEffectiveString(query)) {
      const isCreateReg = /^<(.+)>$/;
      const createMatch = query.match(isCreateReg);
      if (createMatch) {
        //  ('<div>')
        this.dom = document.createElement(createMatch[1]);
      } else {
        // 
        this.dom = document.querySelector(query);
      }
    }
    return this;
  }

  static all(query) {
    return document.querySelectorAll(query);
  }

  static of(str) {
    return new Dom(str);
  }

  static degenerate(query) {
    // 
    // Dom,HTMLElement
    // HTMLElement,
    // queryquerySelector
    let result;
    if (query instanceof Dom) {
      result = query.dom;
    } else if (query instanceof HTMLElement) {
      result = query;
    } else if (isEffectiveString(query)) {
      result = document.querySelector(query);
    } else {
      throw new TypeError(`query${query}`);
    }
    return result;
  }

  attr(key, value) {
    // 
    // keytexthtml innerTextinnerHTML
    if (!isEffectiveString(key)) throw new TypeError(`key ${key}`);
    let result;
    if (key.toLocaleLowerCase().match(/^text$/)) {
      // this.attr(text[,innerText])
      result = this.text(value);
    } else if (key.toLocaleLowerCase().match(/^html$/)) {
      // this.attr(html[,innerHTML])
      result = this.html(value);
    } else if ((this.dom.tagName === 'INPUT') && (key === 'value')) {
      // this.attr(value[, val])
      if (value !== undefined) {
        this.dom.value = String(value);
        result = this;
      } else {
        result = this.dom.value;
      }
    } else if (value !== undefined) {
      // 
      this.dom.setAttribute(key, value);
      result = this;
    } else {
      // 
      result = this.dom.getAttribute(key);
    }
    return result;
  }

  addClass(classes) {
    // class
    const collect = collectClasses(classes);
    collect.forEach((clas) => {
      if (!this.dom.classList.contains(clas)) {
        this.dom.classList.add(clas);
      }
    });
    return this;
  }

  removeClass(classes) {
    // class
    const collect = collectClasses(classes);
    collect.forEach((clas) => {
      if (this.dom.classList.contains(clas)) {
        this.dom.classList.remove(clas);
      }
    });
    return this;
  }

  hasClass(classes) {
    // class classes classestrue
    const collect = collectClasses(classes);
    let result = true;
    collect.forEach((clas) => {
      if (!this.dom.classList.contains(clas)) {
        result = false;
      }
    });
    return result;
  }

  toggleClass(classes) {
    // class classes classes
    const isClassesExist = this.hasClass(classes);
    if (isClassesExist) {
      this.removeClass(classes);
    } else {
      this.addClass(classes);
    }
    return this;
  }

  append(query) {
    //  Selector
    const elements = isEffectiveString(query)
      ? document.querySelectorAll(query)
      : [Dom.degenerate(query)];
    elements.forEach((element) => {
      this.dom.appendChild(element);
    });
    return this;
  }

  remove(query) {
    //  Selector
    const elements = isEffectiveString(query)
      ? this.dom.querySelectorAll(query)
      : [Dom.degenerate(query)];
    elements.forEach((element) => {
      this.dom.removeChild(element);
    });
    return this;
  }

  text(content) {
    // innerText
    let result;
    if (content !== undefined) {
      this.dom.innerText = String(content);
      result = this;
    } else {
      result = this.dom.innerText;
    }
    return result;
  }

  html(content) {
    // innerHTML
    let result;
    if (content !== undefined) {
      this.dom.innerHTML = String(content);
      result = this;
    } else {
      result = this.dom.innerHTML;
    }
    return result;
  }

  child(query) {
    // 
    let element;
    if (isEffectiveString(query)) {
      element = this.dom.querySelector(query);
    } else {
      element = Dom.degenerate(query);
    }
    return element;
  }

  children(selector) {
    //  children()
    let elements;
    if (selector === undefined) {
      elements = this.dom.children();
    } else {
      if (!isEffectiveString(selector)) throw new TypeError(`selector${selector}`);
      elements = this.dom.querySelectorAll(selector);
    }
    return elements;
  }

  parent(query) {
    //  query querySelectorDom
    return Dom.getParent(this.dom, query);
  }

  parents(query) {
    //  query querySelector
    return Dom.getParents(this.dom, query);
  }

  hasParent(query) {
    // query queryquerySelectorDom
    return Dom.hasParent(this.dom, query);
  }

  on(event, callback) {
    // 
    if (!isEffectiveString(event)) throw new TypeError(`event${event}`);
    if (typeof callback !== 'function') throw new TypeError(`callback${callback}`);
    this.dom.addEventListener(event, callback);
    return this;
  }

  off(event, callback) {
    // 
    if (!isEffectiveString(event)) throw new TypeError(`event${event}`);
    if (typeof callback !== 'function') throw new TypeError(`callback${callback}`);
    this.dom.removeEventListener(event, callback);
    return this;
  }

  clear(event) {
    // 
    if (!isEffectiveString(event)) throw new TypeError(`event${event}`);
    this.dom[`on${event}`] = undefined;
    return this;
  }

  static isElement(element) {
    // HTMLElement
    return element && (element.nodeType === 1);
  }

  static getParentsTree(element) {
    // 
    if (!Dom.isElement(element)) throw new TypeError(`element${element}`);
    const tree = [];
    let buffer = element;
    while (buffer.parentElement) {
      buffer = buffer.parentElement;
      tree.push(buffer);
    }
    return tree;
  }

  static getParent(element, query) {
    //  elementHTMLElement, query querySelectorDom
    if (!Dom.isElement(element)) throw new TypeError(`element${element}`);
    let result;
    const tree = Dom.getParentsTree(element);
    const all = document.querySelectorAll(query);
    if (query === undefined) {
      result = element.parentElement;
    } else {
      let parentsCollect;
      if (isEffectiveString(query)) {
        parentsCollect = tree;
      } else if (query instanceof Dom) {
        parentsCollect = [query.dom];
      } else {
        throw new TypeError(`query${query}`);
      }
      parentsCollect.forEach((parent) => {
        all.forEach((item) => {
          if (item.isSameNode(parent)) {
            result = parent;
          }
        });
      });
    }
    return result;
  }

  static getParents(element, query) {
    //  elementHTMLElement, query querySelector
    if (!Dom.isElement(element)) throw new TypeError(`element${element}`);
    let result;
    const tree = Dom.getParentsTree(element);
    const all = document.querySelectorAll(query);
    if (query === undefined) {
      result = tree;
    } else {
      let parentsCollect;
      result = [];
      if (isEffectiveString(query)) {
        parentsCollect = tree;
      } else {
        throw new TypeError(`query${query}`);
      }
      parentsCollect.forEach((parent) => {
        all.forEach((item) => {
          if (item.isSameNode(parent)) {
            result.push(parent);
          }
        });
      });
    }
    return result;
  }

  static hasParent(element, query) {
    // query queryquerySelectorDom
    if ((!isEffectiveString(query)) && !(query instanceof Dom)) throw new TypeError(`query${query}`);
    return !!Dom.getParent(element, query);
  }

  static globalInit() {
    if (isGlobalInited) return false;
    isGlobalInited += 1;
    HTMLCollection.prototype.forEach = Array.prototype.forEach;
    return isGlobalInited;
  }
}

Dom.globalInit();

export default Dom;
